.carrosel_combos {
  max-width: 80vw;
  height: 50vh;
  box-shadow: 2px 2px 5px 3px var(--shadow);
  margin: 30px auto;
  transition: all .6s;
  display: block;
  position: relative;
}

.tela_principal {
  width: 100%;
  height: 100%;
  z-index: -1;
  box-sizing: border-box;
  box-shadow: inset 0px 0px 12px 10px var(--preto);
  background-color: red;
  background-repeat: no-repeat;
  background-size: 65.45%;
  border: 4px solid rgba(0, 0, 0, .698);
  align-content: flex-start;
  justify-content: flex-end;
  align-items: flex-start;
  display: flex;
}

.combos_infos {
  text-align: center;
  text-shadow: 2px 2px 3px var(--branco);
  margin-right: 40px;
}

.combo_preco {
  font-size: 5rem;
  font-family: var(--fonte-secundaria);
  padding: 10% 10% 20px;
}

.combo_complementos {
  font-size: 1.25rem;
  font-family: var(--fonte-secundaria);
  padding: 0 5px;
}

.tela_hover {
  width: 80vw;
  height: 50vh;
  z-index: 2;
  opacity: 0;
  color: #fff;
  box-sizing: border-box;
  text-transform: uppercase;
  background-image: linear-gradient(rgba(0, 0, 0, .424), rgba(0, 0, 0, .855) 80%);
  align-items: flex-end;
  padding: 20px 25px;
  font-family: Courier New, Courier, monospace;
  font-size: 1.85rem;
  transition: all .5s;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
}

.movimentador-carrosel > .proximo, .movimentador-carrosel > .anterior {
  z-index: 10;
  padding: 5px;
  font-size: 4rem;
  transition: all .4s;
  position: absolute;
  top: 40%;
}

.movimentador-carrosel > .proximo {
  right: 0;
}

.movimentador-carrosel > .proximo:hover, .movimentador-carrosel > .anterior:hover {
  text-shadow: 0 0 5px #fff, 0 0 10px #fff, 0 0 15px #fff;
  cursor: pointer;
}

